/* For Login Page */

/* Import */
@import url('https://fonts.googleapis.com/css?family=Roboto:400,500');
@import url('https://fonts.googleapis.com/css?family=Montserrat:100,200,300,400,500,700');
/* Reset */
* {
  box-sizing: border-box;
}
body {
  font-family: Montserrat, Roboto, sans-serif;
  margin: 0;
}

a {
  color: #0ab7f4;
  text-decoration: none;
}

/* Base */
.login {
  background-color: #f8f8f8;
  /* border: 1px solid red; */
  display: flex;
    justify-content: center;
    align-items: center;
  padding: 4% 0;
}

.login-box {
  background-color: #ffffff;
  border: 1px solid #e6e6e6;
  display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
  padding: 15px 4%;
  width: 500px;
}

.login-box img {
  height: calc(165px / 2);
  margin-top: 20px;
  margin-bottom: 30px;
  width: calc(330px / 2);
}

.h1 {
  text-align: center;
}

input[type="email"], input[type="password"] {
  display: block;
  height: 45px;
  margin-bottom: 35px;
  padding: 6px 0 6px 16px;
  width: 100%;
}

.label {
  font-weight: bold;
  text-align: start;
  text-transform: uppercase;
  width: 100%;
}

.form-row {
  display: flex;
    justify-content: space-between;
  width: 100%;
  margin-top: 10px;
  margin-bottom: 20px;
}

.btn-signin {
  background-color: #707070;
  background-image: linear-gradient(to right, #00c352, #00c698 79%, #00c7b8);
  cursor: pointer;
  color: #ffffff;
  border: none;
  height: 50px;
  /* border-radius: 5px; */
  border-radius: 35px;
  /* text-transform: uppercase; */
  margin-bottom: 20px;
  width: 200px;
}
